.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button.back-to-top{
  position: fixed;
  right: 20px;
  bottom: 60px;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: none;
  outline: none;
  width: 30px;
  height: 30px;
  color: #fff;
  background-color: var(--primary-color);
  font-size: 1.15rem;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}
button.back-to-top:hover{
  background-color: var(--background-color-btn);
}
button.back-to-top.btn--show{
  opacity: 1;
  visibility: visible;
}